.#{$identifier}-math-game {
  &-header {
    display: flex;
    justify-content: space-between; /* Space out stats horizontally */
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
    border-bottom: 2px solid #fff;
  }

  .#{$identifier}-equation-wrapper {
    .#{$identifier}-equation-question {
      padding: 80px;

      @media (max-width: 480px) {
        padding: 48px 0px;
      }

      .#{$identifier}-number {
        font-size: 2.5em;
        text-shadow: 2px 2px 2px $black;
      }
      .#{$identifier}-operation {
        font-size: 2.5em;
        padding: 0px 10px;
        text-shadow: 2px 2px 2px $black;
      }
    }

    .#{$identifier}-equation-answer {
      @media (max-width: 480px) {
        display: flex;
      }
      .#{$identifier}-btn {
        background-color: transparent;
        border: 2px solid;
        text-shadow: 2px 2px 2px $black;
        font-size: 2em;
        margin: 0px 20px;
        padding: 15px 30px;

        @media (max-width: 480px) {
          padding: 16px 24px;
        }

        &:hover {
          background-color: $light-overlay;
        }
      }
    }
  }

  .#{$identifier}-gameover-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 10;
    @media (max-width: 480px) {
      h1,
      h2 {
        margin: 0px;
      }
    }
  }

  .#{$identifier}-gameover-message-container {
    display: flex;
    flex-direction: row;
    gap: 100px;
    @media (max-width: 480px) {
      padding: 24px;
      width: calc(100% - 48px);
      gap: 20px;
      flex-wrap: wrap;
      justify-content: center;
    }
  }

  .#{$identifier}-btns-container {
    @media (max-width: 480px) {
      width: calc(100% - 48px);
      margin-top: 0px;
      padding: 0px 24px;
      display: flex;
      gap: 20px;
    }
  }
}
